import React, { useRef, useImperativeHandle, forwardRef } from 'react';
import clsx from 'clsx';
import { GroupDefaultProps, GroupPropTypes } from './generated/propTypes';

const getSecondaryAlignment = align => {
    switch (align) {
        case 'end':
            return 'flex-end';
        case 'start':
            return 'flex-start';
        case 'center':
            return 'center';
        case 'stretch':
            return 'stretch';
        case 'baseline':
        default:
            return 'baseline';
    }
};

const getPrimaryAlignment = justification => {
    switch (justification) {
        case 'end':
            return 'flex-end';
        case 'center':
            return 'center';
        case 'space-evenly':
            return 'space-evenly';
        case 'space-between':
            return 'space-between';
        case 'space-around':
            return 'space-around';
        case 'start':
        default:
            return 'flex-start';
    }
};

const getPadding = padding => {
    switch (padding) {
        case 'dense':
            return '8px';
        case 'airy':
            return '24px';
        case 'standard':
            return '16px';
        case 'none':
        default:
            return '0px';
    }
};

const getDirection = direction => {
    switch (direction) {
        case 'horizontal':
            return 'row';
        case 'vertical':
            return 'column';
        case 'horizontal-reverse':
            return 'row-reverse';
        case 'vertical-reverse':
            return 'column-reverse';
        default:
            return 'row';
    }
};

const Group = forwardRef((props, externalRef) => {
    const {
        children,
        className,
        direction,
        fullWidth,
        gap,
        padding,
        primaryAlign,
        secondaryAlign,
        style,
        variant,
        ...customProps
    } = props;
    const styleActual = {
        display: 'flex',
        flexDirection: getDirection(direction),
        justifyContent: getPrimaryAlignment(primaryAlign),
        alignItems: getSecondaryAlignment(secondaryAlign),
        gap: getPadding(padding || gap),
        width: fullWidth ? '100%' : '',
        ...style
    };

    const ref = useRef();

    useImperativeHandle(externalRef, () => ref.current, []);

    return (
        <div
            ref={ref}
            className={clsx(
                'lmnt',
                'lmnt-group',
                // variant === 'dense' && 'lmnt-button-group--dense',
                // variant === 'standard' && 'lmnt-button-group--standard',
                // variant === 'airy' && 'lmnt-button-group--airy',
                className
            )}
            style={styleActual}
            {...customProps}
        >
            {children}
        </div>
    );
});

Group.displayName = 'Group';
Group.propTypes = GroupPropTypes;
Group.defaultProps = GroupDefaultProps;

export default Group;
